import React from 'react';
import PropTypes from 'prop-types'; // Import PropTypes
import { ChevronLeftIcon, ChevronRightIcon } from '@heroicons/react/24/outline';

const Pagination = ({ page, totalPages, onPageChange }) => {
  if (totalPages <= 1) return null; // No pagination if only one page

  const maxVisiblePages = 3;
  const pages = [];

  // Calculate the start and end pages
  let startPage = Math.max(1, page - Math.floor(maxVisiblePages / 2));
  let endPage = Math.min(totalPages, startPage + maxVisiblePages - 1);

  // Adjust if pages are less than maxVisiblePages
  if (endPage - startPage < maxVisiblePages - 1) {
    startPage = Math.max(1, endPage - maxVisiblePages + 1);
  }

  // Render the first page button and ellipsis if necessary
  if (startPage > 1) {
    pages.push(
      <button
        key="first"
        onClick={() => onPageChange(1)}
        className="relative inline-flex items-center px-4 py-2 text-md font-semibold text-gray-900 dark:text-gray-100 ring-1 ring-inset ring-gray-300 dark:ring-gray-700 hover:bg-gray-50 dark:hover:bg-gray-700 focus:z-20 focus:outline-offset-0"
      >
        1
      </button>
    );
    if (startPage > 2) {
      pages.push(
        <span
          key="dots-start"
          className="relative inline-flex items-center px-4 py-2 text-md font-semibold text-gray-700 dark:text-gray-300 ring-1 ring-inset ring-gray-300 dark:ring-gray-700 focus:outline-offset-0"
        >
          ...
        </span>
      );
    }
  }

  // Render the visible page buttons
  for (let i = startPage; i <= endPage; i++) {
    pages.push(
      <button
        key={i}
        onClick={() => onPageChange(i)}
        className={`relative inline-flex items-center px-4 py-2 text-md font-semibold ${
          page === i
            ? 'bg-blue-600 text-white dark:bg-blue-500 dark:text-white'
            : 'text-gray-900 dark:text-gray-100 ring-1 ring-inset ring-gray-300 dark:ring-gray-700 hover:bg-gray-50 dark:hover:bg-gray-700'
        } focus:z-20 focus:outline-offset-0`}
      >
        {i}
      </button>
    );
  }

  // Render the last page button and ellipsis if necessary
  if (endPage < totalPages) {
    if (endPage < totalPages - 1) {
      pages.push(
        <span
          key="dots-end"
          className="relative inline-flex items-center px-4 py-2 text-md font-semibold text-gray-700 dark:text-gray-300 ring-1 ring-inset ring-gray-300 dark:ring-gray-700 focus:outline-offset-0"
        >
          ...
        </span>
      );
    }
    pages.push(
      <button
        key="last"
        onClick={() => onPageChange(totalPages)}
        className="relative inline-flex items-center px-4 py-2 text-md font-semibold text-gray-900 dark:text-gray-100 ring-1 ring-inset ring-gray-300 dark:ring-gray-700 hover:bg-gray-50 dark:hover:bg-gray-700 focus:z-20 focus:outline-offset-0"
      >
        {totalPages}
      </button>
    );
  }

  return (
    <nav
      className="isolate inline-flex -space-x-px rtl:space-x-reverse rounded-md shadow-sm"
      aria-label="Pagination"
    >
      <button
        onClick={() => onPageChange(page - 1)}
        disabled={page === 1}
        className="relative inline-flex items-center px-2 py-2 text-gray-400 dark:text-gray-500 ring-1 ring-inset ring-gray-300 dark:ring-gray-700 hover:bg-gray-50 dark:hover:bg-gray-700 focus:z-20 focus:outline-offset-0"
      >
        <span className="sr-only">Previous</span>
        <ChevronLeftIcon className="h-5 w-5 rtl:rotate-180" />
      </button>
      {pages}
      <button
        onClick={() => onPageChange(page + 1)}
        disabled={page === totalPages}
        className="relative inline-flex items-center px-2 py-2 text-gray-400 dark:text-gray-500 ring-1 ring-inset ring-gray-300 dark:ring-gray-700 hover:bg-gray-50 dark:hover:bg-gray-700 focus:z-20 focus:outline-offset-0"
      >
        <span className="sr-only">Next</span>
        <ChevronRightIcon className="h-5 w-5 rtl:rotate-180" />
      </button>
    </nav>
  );
};

Pagination.propTypes = {
  page: PropTypes.number.isRequired,
  totalPages: PropTypes.number.isRequired,
  onPageChange: PropTypes.func.isRequired,
};

export default Pagination;
