import React, { useState, useEffect } from 'react';
import parse from 'html-react-parser';
import { useTranslation } from 'react-i18next';
import { useDarkMode } from '../../DarkModeContext'; // Import the custom hook

const Banners = () => {
  const { t } = useTranslation();
  const { isDarkMode } = useDarkMode();
  const [banners, setBanners] = useState([]);

  useEffect(() => {
    setBanners([
      {
        title: t(
          'Discover Key Insights, Identify Top Fans & Boost Your Best Content'
        ),
        desc: t(
          'Unlock WP ULike Pro—Turn Your Data into Actionable Insights. Instantly find out what your users love and fine-tune your content for maximum engagement. Elevate your performance with detailed reports and real-time analysis.'
        ),
        image: isDarkMode
          ? 'https://wpulike.com/wp-content/uploads/2024/08/dark-stats.png'
          : 'https://wpulike.com/wp-content/uploads/2024/08/light-stats.png',
        buttons: [
          {
            title: t('Unlock WP ULike Pro'),
            url: 'https://wpulike.com/pricing/?utm_source=statistics-page&utm_campaign=gopro&utm_medium=wp-dash',
          },
          {
            title: t('Discover More Features'),
            url: 'https://wpulike.com/blog/wp-ulike-pro-statistics/?utm_source=statistics-page&utm_campaign=gopro&utm_medium=wp-dash',
          },
        ],
      },
    ]);
  }, [isDarkMode, t]); // Update banners when isDarkMode or t changes

  return (
    <div>
      {banners.map((banner, index) => (
        <div key={index} className="mt-5 mb-5">
          <div className="mx-auto">
            <div className="overflow-hidden rounded-lg bg-blue-600 shadow-xl lg:grid lg:grid-cols-2 lg:gap-4">
              <div className="px-6 pb-12 pt-10 sm:px-16 sm:pt-16 lg:py-16 lg:pr-0 xl:px-20 xl:py-20">
                <div className="lg:self-center">
                  <h2 className="text-2xl font-bold tracking-tight text-white">
                    <span className="block">{parse(banner.title)}</span>
                  </h2>
                  <p className="mt-4 text-base leading-6 text-blue-200">
                    {parse(banner.desc)}
                  </p>
                  <div className="mt-5 flex flex-wrap items-center gap-x-3">
                    {banner.buttons.map((button, index) => (
                      <a
                        key={index}
                        target="_blank"
                        rel="noreferrer"
                        href={parse(button.url)}
                        className="mt-3 inline-flex items-center rounded-md border border-transparent bg-white px-3 py-3 text-sm font-bold text-blue-600 shadow hover:bg-blue-100"
                      >
                        {parse(button.title)}
                      </a>
                    ))}
                  </div>
                </div>
              </div>
              <div className="aspect-h-3 aspect-w-5 -mt-6 md:aspect-h-1 md:aspect-w-2">
                <img
                  className="translate-x-0 translate-y-10 transform rounded-md object-cover object-left-top sm:translate-y-16 lg:translate-y-15 lg:translate-x-16"
                  src={parse(banner.image)}
                  alt="screenshot"
                />
              </div>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default Banners;
