import React, { useState } from 'react';
import PropTypes from 'prop-types'; // Import PropTypes
import { HandThumbUpIcon } from '@heroicons/react/24/outline';
import parse from 'html-react-parser';
import { capitalizeFirstLetter } from '../../utils/functions';
import { useTranslation } from 'react-i18next';
import SelectField from '../common/SelectField';
import CountDisplay from '../common/CountDisplay';
import classNames from 'classnames';

const Items = ({ initialData }) => {
  const categories = Object.keys(initialData).filter(
    (category) => initialData[category].length > 0
  );
  const { t } = useTranslation();

  const [currentTab, setCurrentTab] = useState(categories[0]);

  const handleTabClick = (e, category) => {
    e.preventDefault();
    setCurrentTab(category);
  };

  // Create options for react-select
  const options = categories.map((category) => ({
    value: category,
    label: `${t('Top')} ${t(
      capitalizeFirstLetter(
        category
          .replace(/\bpost\b/gi, 'posts')
          .replace(/\bcomment\b/gi, 'comments')
          .replace(/\bactivity\b/gi, 'activities')
          .replace(/\btopic\b/gi, 'topics')
      )
    )}`,
  }));

  return (
    <div className="p-8 shadow-xl rounded-lg bg-white dark:bg-gray-800 mt-10 mb-10">
      <div className="overflow-hidden">
        <div>
          {/* Tab Navigation */}
          <div className="sm:hidden">
            <SelectField
              options={options}
              value={options.find((option) => option.value === currentTab)}
              onChange={(selectedOption) => setCurrentTab(selectedOption.value)}
              closeMenuOnSelect={true}
              hideSelectedOptions={false}
            />
          </div>

          <div className="hidden sm:block">
            <div className="border-b border-gray-200 dark:border-gray-700">
              <nav className="-mb-px flex" aria-label="Tabs">
                {categories.map((category) => (
                  <button
                    key={category}
                    href="#"
                    onClick={(e) => handleTabClick(e, category)}
                    className={classNames(
                      category === currentTab
                        ? 'border-blue-500 text-blue-600 dark:border-blue-400 dark:text-blue-400'
                        : 'border-transparent text-gray-500 hover:border-gray-300 hover:text-gray-700 dark:border-transparent dark:text-gray-400 dark:hover:border-gray-600 dark:hover:text-gray-300',
                      'w-1/4 border-b-2 py-4 px-1 text-center text-sm font-medium'
                    )}
                    aria-current={category === currentTab ? 'page' : undefined}
                  >
                    {t('Top')}{' '}
                    {t(
                      capitalizeFirstLetter(
                        category
                          .replace(/\bpost\b/gi, 'posts')
                          .replace(/\bcomment\b/gi, 'comments')
                          .replace(/\bactivity\b/gi, 'activities')
                          .replace(/\btopic\b/gi, 'topics')
                      )
                    )}
                  </button>
                ))}
              </nav>
            </div>
          </div>
        </div>

        <div className="space-y-2 p-4 mt-4">
          {initialData[currentTab]?.length > 0 && (
            <ul className="divide-y divide-gray-100 dark:divide-gray-700">
              {(initialData[currentTab] || []).map((item, index) => (
                <li
                  key={index}
                  className="flex flex-wrap items-center justify-between gap-x-6 gap-y-4 py-5 sm:flex-nowrap"
                >
                  <div className="flex items-center gap-x-3">
                    <div className="min-w-0 list-decimal text-sm font-semibold leading-6 text-gray-900 dark:text-gray-100">
                      {++index}.
                    </div>
                    <div className="min-w-0 text-sm font-semibold leading-6 text-gray-900 dark:text-gray-100">
                      <a
                        target="_blank"
                        rel="noreferrer"
                        href={parse(item.permalink)}
                        className="hover:underline dark:hover:text-blue-400"
                      >
                        {parse(item.title)}
                      </a>
                    </div>
                  </div>
                  <dl className="flex w-full flex-none justify-between gap-x-8 sm:w-auto">
                    <CountDisplay
                      count={item.likes_count ?? 0}
                      Icon={HandThumbUpIcon}
                      iconColorClass="text-green-800 dark:text-green-400"
                      textColorClass="text-green-900 dark:text-green-300"
                    />
                  </dl>
                </li>
              ))}
            </ul>
          )}
        </div>
      </div>
    </div>
  );
};

// Define PropTypes for Items
Items.propTypes = {
  initialData: PropTypes.objectOf(
    PropTypes.arrayOf(
      PropTypes.shape({
        permalink: PropTypes.string,
        title: PropTypes.string,
        likes_count: PropTypes.number,
      })
    )
  ).isRequired,
};

export default Items;
