import React from 'react';
import PropTypes from 'prop-types';

const CountDisplay = ({ count, Icon, iconColorClass, textColorClass }) => {
  if (count <= 0) return null;

  return (
    <div className="flex w-12 gap-x-1">
      <dt>
        <Icon className={`h-6 w-6 ${iconColorClass}`} aria-hidden="true" />
      </dt>
      <dd className={`text-sm leading-6 ${textColorClass}`}>{count}</dd>
    </div>
  );
};

CountDisplay.propTypes = {
  count: PropTypes.number.isRequired,
  Icon: PropTypes.elementType.isRequired,
  iconColorClass: PropTypes.string.isRequired,
  textColorClass: PropTypes.string.isRequired,
};

export default CountDisplay;
