import React from 'react';
import PropTypes from 'prop-types'; // Import PropTypes
import { useTranslation } from 'react-i18next';
import DataSection from './DataSection'; // Ensure DataSection is correctly imported

const Metrics = ({ initialData }) => {
  const { t } = useTranslation();

  const sections = [
    { title: t('Engagement This Week'), value: initialData.week },
    { title: t('Monthly Engagement Overview'), value: initialData.month },
    { title: t('Yearly Engagement Trends'), value: initialData.year },
    { title: t('Overall Performance'), value: initialData.all },
  ];

  return (
    <dl className="mx-auto border border-gray-200 grid grid-cols-1 gap-px bg-gray-200 dark:bg-gray-700 dark:border-gray-700 sm:grid-cols-2 lg:grid-cols-4 mb-5">
      {sections.map((section, index) => (
        <DataSection key={index} title={section.title} value={section.value} />
      ))}
    </dl>
  );
};

// Define PropTypes for Metrics
Metrics.propTypes = {
  initialData: PropTypes.shape({
    week: PropTypes.number,
    month: PropTypes.number,
    year: PropTypes.number,
    all: PropTypes.number,
  }).isRequired,
};

export default Metrics;
