import React from 'react';
import Select from 'react-select';
import { useTranslation } from 'react-i18next';
import { useDarkMode } from '../../DarkModeContext'; // Import the custom hook
import PropTypes from 'prop-types'; // Import PropTypes

// Function to get dark mode styles
const getDarkModeStyles = (isDarkMode) => ({
  control: (provided) => ({
    ...provided,
    minHeight: '2.5rem',
    transition: 'none',
    borderRadius: '0.375rem',
    borderColor: isDarkMode ? '#4B5563' : '#d1d5db', // Dark mode border color
    boxShadow: isDarkMode
      ? '0 1px 2px rgba(255, 255, 255, 0.1)'
      : '0 1px 2px rgba(0, 0, 0, 0.05)', // Dark mode shadow
    backgroundColor: isDarkMode ? '#374151' : '#ffffff', // Dark mode background
    '&:hover': {
      borderColor: '#1C64F2',
    },
  }),
  valueContainer: (provided) => ({
    ...provided,
    height: '100%',
    padding: '0 0.75rem',
  }),
  multiValueLabel: (base) => ({
    ...base,
    whiteSpace: 'normal',
    overflow: 'visible',
  }),
  input: (provided) => ({
    ...provided,
    margin: 0,
    'input:focus': {
      boxShadow: 'none',
    },
  }),
  placeholder: (provided) => ({
    ...provided,
    margin: 0,
  }),
  singleValue: (provided) => ({
    ...provided,
    margin: 0,
  }),
});

const SelectField = ({
  options,
  value,
  onChange,
  isMulti = false,
  closeMenuOnSelect = false,
  hideSelectedOptions = false,
}) => {
  const { t } = useTranslation();
  const { isDarkMode } = useDarkMode();

  return (
    <Select
      styles={getDarkModeStyles(isDarkMode)}
      options={options}
      value={value}
      onChange={onChange}
      placeholder={t('Select...')}
      isMulti={isMulti}
      closeMenuOnSelect={closeMenuOnSelect}
      hideSelectedOptions={hideSelectedOptions}
      className="mt-1 block w-full"
    />
  );
};

// Define prop types
SelectField.propTypes = {
  options: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.string.isRequired,
      label: PropTypes.string.isRequired,
    })
  ).isRequired,
  value: PropTypes.oneOfType([
    PropTypes.object,
    PropTypes.arrayOf(PropTypes.object),
  ]),
  onChange: PropTypes.func.isRequired,
  isMulti: PropTypes.bool,
  closeMenuOnSelect: PropTypes.bool,
  hideSelectedOptions: PropTypes.bool,
};

export default SelectField;
