import React from 'react';
import PropTypes from 'prop-types'; // Import PropTypes
import { formatNumber } from '../../utils/functions';

const DataSection = ({ title, value }) => {
  return (
    <div className="flex flex-wrap items-baseline justify-between gap-x-2 gap-y-2 bg-white dark:bg-gray-800 px-4 py-10 sm:px-6 xl:px-8">
      <dd className="w-full flex-none text-5xl font-extrabold leading-10 tracking-tight text-gray-900 dark:text-gray-100">
        {value !== undefined ? formatNumber(value) : 'No Data'}
      </dd>
      <dt
        className="text-sm font-normal leading-6 text-gray-500 dark:text-gray-400"
        dangerouslySetInnerHTML={{ __html: title }}
      />
    </div>
  );
};

// Define PropTypes for DataSection
DataSection.propTypes = {
  title: PropTypes.string.isRequired,
  value: PropTypes.number, // Not required, can be undefined
};

export default DataSection;
