import React, { useState, useEffect } from 'react';
import ReactApexChart from 'react-apexcharts';
import { useTranslation } from 'react-i18next';
import { capitalizeFirstLetter } from '../../utils/functions';
import { useDarkMode } from '../../DarkModeContext'; // Import the custom hook
import PropTypes from 'prop-types'; // Import PropTypes

const Charts = ({ initialData, initialCategory }) => {
  const [chartOptions, setChartOptions] = useState({});
  const [chartSeries, setChartSeries] = useState([]);
  const { isDarkMode } = useDarkMode();
  const { t } = useTranslation();

  useEffect(() => {
    const parsedData =
      initialData && Array.isArray(initialData)
        ? initialData.sort((a, b) => new Date(a.date) - new Date(b.date))
        : [];

    const totals = parsedData.map((item) => item.total);

    setChartOptions({
      chart: {
        fontFamily: 'Roboto',
        type: 'line',
        toolbar: {
          show: false,
        },
        zoom: false,
      },
      colors: ['#1C64F2'],
      dataLabels: {
        enabled: true,
      },
      stroke: {
        curve: 'smooth',
      },
      xaxis: {
        type: 'datetime',
        categories: parsedData.map((item) => item.date),
        title: {
          text: t('Dates'),
          style: {
            cssClass: 'fill-gray-500 dark:fill-gray-400',
          },
        },
        labels: {
          style: {
            cssClass: 'fill-gray-500 dark:fill-gray-400',
          },
        },
        axisBorder: {
          show: false,
        },
      },
      yaxis: {
        type: 'numeric',
        title: {
          text: t('Totals'),
          style: {
            cssClass: 'fill-gray-500 dark:fill-gray-400',
          },
        },
        labels: {
          formatter: function (value) {
            return Math.round(value);
          },
          style: {
            cssClass: 'fill-gray-500 dark:fill-gray-400',
          },
        },
        min: 0,
        forceNiceScale: true,
      },
      markers: {
        size: 1,
      },
      tooltip: {
        theme: isDarkMode ? 'dark' : 'light',
      },
      grid: {
        borderColor: isDarkMode ? '#374151' : '#D1D5DB',
        strokeDashArray: 4,
        xaxis: {
          lines: {
            show: true,
          },
        },
      },
      noData: {
        text: t('No data to display'),
        style: {
          color: isDarkMode ? '#ffffff' : '#000000',
        },
      },
    });

    setChartSeries([
      {
        name: t(capitalizeFirstLetter(initialCategory)),
        data: totals,
      },
    ]);
  }, [initialData, initialCategory, t, isDarkMode]);

  return (
    <div className="mb-5">
      <ReactApexChart
        className="mt-5"
        options={chartOptions}
        series={chartSeries}
        type="line"
        height={330}
      />
    </div>
  );
};

Charts.propTypes = {
  initialData: PropTypes.arrayOf(
    PropTypes.shape({
      date: PropTypes.string.isRequired,
      [PropTypes.string]: PropTypes.number, // Allow additional keys with numeric values
    })
  ).isRequired,
  initialCategory: PropTypes.string.isRequired,
};

export default Charts;
