import React from 'react';
import PropTypes from 'prop-types'; // Import PropTypes
import { useTranslation } from 'react-i18next';
import { formatNumber } from '../../utils/functions';

import {
  FolderIcon,
  ArrowTrendingUpIcon,
  CalendarIcon,
} from '@heroicons/react/24/outline';

const StatSection = ({ icon: Icon, title, value }) => {
  return (
    <div className="relative overflow-hidden rounded-lg bg-white dark:bg-gray-800 px-4 pb-5 pt-5 shadow-xl sm:px-6 sm:pt-6">
      <dt>
        <div className="absolute rounded-md bg-blue-500 p-2.5">
          <Icon className="h-8 w-8 text-white" />
        </div>
        <p
          className="ml-20 rtl:ml-0 rtl:mr-20 truncate text-sm font-normal text-gray-600 dark:text-gray-300"
          dangerouslySetInnerHTML={{ __html: title }}
        />
      </dt>
      <dd className="ml-20 rtl:ml-0 rtl:mr-20 flex items-center">
        <p className="text-3xl font-extrabold text-gray-900 dark:text-gray-100">
          {formatNumber(value)}
        </p>
      </dd>
    </div>
  );
};

// Define PropTypes for StatSection
StatSection.propTypes = {
  icon: PropTypes.elementType, // Expect a React component (icon)
  title: PropTypes.string,
  value: PropTypes.number,
};

const Overview = ({ initialData }) => {
  const { t } = useTranslation();

  const sections = [
    {
      icon: ArrowTrendingUpIcon,
      title: t('Total Interactions To Date'),
      value: initialData.total ?? 0,
    },
    {
      icon: CalendarIcon,
      title: t("Today's Engagement Overview"),
      value: initialData.today ?? 0,
    },
    {
      icon: FolderIcon,
      title: t("Yesterday's Engagement Summary"),
      value: initialData.yesterday ?? 0,
    },
  ];

  return (
    <div>
      <dl className="mt-10 grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-3">
        {sections.map((section, index) => (
          <StatSection
            key={index}
            icon={section.icon}
            title={section.title}
            value={section.value}
          />
        ))}
      </dl>
    </div>
  );
};

// Define PropTypes for Overview
Overview.propTypes = {
  initialData: PropTypes.shape({
    total: PropTypes.number,
    today: PropTypes.number,
    yesterday: PropTypes.number,
  }).isRequired,
};

export default Overview;
